.slick-slide {
  transform: scale(0.8);
  transition: all 0.4s ease-in-out;
  padding: 40px 0;
}

.slick-slide img {
  max-width: 100%;
  transition: all 0.4s ease-in-out;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 7px;
}

.slick-center {
  transform: scale(1.1);
}
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f8f8;
}

section {
  padding-bottom: 1em;
  text-align: center;
}

section p {
  font-family: Monsieur La Doulaise;
  font-weight: bold;
  font-size: 25px;
}

section h2 {
  font-family: Monsieur La Doulaise;
  font-weight: bold;
  font-size: 40px;
}

.box {
  margin: 1em;
  padding: 1em;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 10px;
  border-color: #114835;
}
