.headerImage {
  background-image: url("../../assets/backgroundImg2.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  margin-bottom: 1em;
}

h1 img {
  width: 60%;
  height: auto;
}

header {
  color: #13513c;
  text-align: center;
  padding: 0.5em;
  font-size: 30px;
  font-family: Monsieur La Doulaise;
}

nav {
  display: flex;
  justify-content: center;
  padding: 0.5em;
}

nav a {
  color: #cfbd76;
  font-family: bold;
  text-decoration: none;
  padding: 0.5em 1em;
  margin: 0 0.25em;
  border-radius: 5px;
  transition: background-color 0.3s;
}

nav a:hover {
  background-color: #13513c;
}
