.container {
  padding: 40px 5%;
}

ul {
  list-style: none;
}

/* Responsive image gallery rules begin*/

.image-gallery {
  margin: 1em;
  padding: 1em;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 10px;
  background-color: #114835;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.image-gallery > li {
  flex-basis: 250px; /*width: 350px;*/
  position: relative;
  cursor: pointer;
}

.image-gallery::after {
  content: "";
  flex-basis: 250px;
}

.image-gallery li img {
  object-fit: cover;
  width: 100%;
  height: auto;
  vertical-align: middle;
  border-radius: 5px;
  box-shadow: 3px 4px 5px 0px rgba(0, 0, 0, 0.38);
}

/* hover */
.image-gallery li:hover .overlay {
  transform: scale(1);
}
